import * as ethers from "ethers";
import Safe, { EthersAdapter } from "@safe-global/protocol-kit";
import SafeApiKit from "@safe-global/api-kit";
import { OperationType, SafeTransaction, SafeTransactionDataPartial, SafeSignature } from "@safe-global/safe-core-sdk-types";

// +---------------+
// | Configuration |
// +---------------+

function choose(
  network: string,
  goerli: string,
  mainnet: string,
  addresses: boolean = true,
): string {
  switch (network) {
        case "goerli":
          return addresses ? ethers.utils.getAddress(goerli) : goerli;
        case "mainnet":
          return addresses ? ethers.utils.getAddress(mainnet) : mainnet;
  }
  throw new Error(`invalid network: '${network}'`);
}

const getTxService = (network: string): string => choose(
  network,
  "https://safe-transaction-goerli.safe.global/",
  "https://safe-transaction-mainnet.safe.global/",
  false
);

// +-----------+
// | Contracts |
// +-----------+

