function prepAddress(address: string): string {
    return ethers.utils.hexlify(
        ethers.utils.zeroPad(
            ethers.utils.getAddress(address),
            32,
        ),
    ).substring(2);
}

function prepIntger(value: ethers.BigNumberish): string {
    return ethers.utils.hexlify(
        ethers.utils.zeroPad(
            ethers.BigNumber.from(value).toHexString(),
            32,
        ),
    ).substring(2);
}

function buildWithdraw(network: string, value: ethers.BigNumberish): string {
    const mid: string = "f3fef3a3";
    const one: string = prepAddress(getNewEscrow(network));
    const two: string = prepIntger(value);
    return `0x${mid}${one}${two}`;
}

function buildAddOracle(address: string): string {
    const mid: string = "df5dd1a5";
    const arg: string = ethers.utils.hexlify(ethers.utils.zeroPad(ethers.utils.getAddress(address), 32)).substring(2);
    return `0x${mid}${arg}`;
}

function buildUpdateWhitelist(address: string, approved: boolean): string {
    const mid = "ac1d0609";
    const one = prepAddress(address);
    const two = prepIntger(Number(approved));
    return `0x${mid}${one}${two}`;
}

function buildRewardEthBalanceOf(address: string): string {
  const mid = "70a08231";
  const one = prepAddress(address);
  return `0x${mid}${one}`;
}

function buildRewardEthTransfer(address: string, amount: ethers.BigNumberish): string {
  const mid = "a9059cbb";
  const one = prepAddress(address);
  const two = prepIntger(amount);
  return `0x${mid}${one}${two}`;
}

// +------+
// | URLs |
// +------+

const getMultisigURL = (network: string): string => {
  const prefix: string = choose(network, "gor", "eth", false);
  const multisig: string = getMultisig(network);
  return `https://app.safe.global/home?safe=${prefix}:${multisig}`;
}

// +-----------+
// | Utilities |
// +-----------+

function ethereum(): any {
  if ("ethereum" in window) {
    return window["ethereum"];
  }
  return null;
}

function makeProvider(network: string): ethers.providers.Web3Provider {
  const provider: ethers.providers.Web3Provider = new ethers.providers.Web3Provider(
    ethereum(),
    network,
  );
  return provider;
}

// +--------+
// | Public |
// +--------+

export async function getBalance(network: string): Promise<ethers.BigNumber> {
  const provider: ethers.providers.Web3Provider = makeProvider(network);
  const balance: ethers.BigNumber = await provider.getBalance(getOldEscrow(network));
  return balance;
}

export async function getRewardBalance(network: string, address: string): Promise<ethers.BigNumber> {
  const provider: ethers.providers.Web3Provider = makeProvider(network);
  // buildRewardBalanceOf(address)
  const res: string = await provider.call({
    to: getRewardETH(network),
    data: buildRewardEthBalanceOf(address),
  });
  return ethers.BigNumber.from(res);
}

// +-----------+
// | Proposals |
// +-----------+

async function propose(network: string, to: string, data: string): Promise<string> {
  const provider: ethers.providers.Web3Provider = makeProvider(network);
  const signer: ethers.providers.JsonRpcSigner = provider.getSigner();
  const ethAdapter: EthersAdapter = new EthersAdapter({ ethers, signerOrProvider: signer });
  const safeAddress: string = getMultisig(network);
  const safe: Safe = await Safe.create({ ethAdapter, safeAddress });
  const kit = new SafeApiKit({
    txServiceUrl: getTxService(network),
    ethAdapter,
  });
  const safeTransactionData: SafeTransactionDataPartial = {
    to,
    value: "0",
    data,
    operation: OperationType.Call
  };
  const tx: SafeTransaction = await safe.createTransaction({ safeTransactionData });
  const safeTxHash: string = await safe.getTransactionHash(tx);
  let senderSignature: SafeSignature
  try {
    senderSignature = await safe.signTransactionHash(safeTxHash);
  } catch (e) {
    // Let's see if the user rejected signing the tx.  If yes, leave
    // this function quietly.
    const msg = "" + e;
    if (msg.indexOf("user rejected signing") !== -1) {
      console.log("[INFO] User rejected signing.");
    } else {
      console.log(e);
    }
    return "";
  }
  const senderAddress: string = await signer.getAddress();
  const url = getMultisigURL(network);

  console.log("Proposing transaction:");
  console.log("  - method: Oracles.addOracle");
  console.log(`  - to: ${to}`);
  console.log(`  - data: ${data}`);
  console.log(`  - network: ${network}`);
  console.log(`  - safeAddress: ${safeAddress}`);
  console.log(`  - safeTxHash: ${safeTxHash}`);
  console.log(`  - sender: ${senderAddress}`);
  console.log(`  - signature: ${senderSignature.data}`);
  console.log(`  - multisig url: ${url}`);

  try {
    await kit.proposeTransaction({
      safeAddress,
      safeTransactionData: tx.data,
      safeTxHash,
      senderAddress,
      senderSignature: senderSignature.data,
    });
  } catch (e) {
    const msg = "" + e;
    if (msg.indexOf("is not an owner or delegate") !== -1) {
      console.log("[WARNING] Wallet is not an owner or delegate.");
      return "Error: Wallet is not an owner or a delegate.";
    } else {
      console.log(e);
    }
  }

  return url;
}

export async function proposeMove(network: string, amount: ethers.BigNumberish): Promise<string> {
    return propose(
        network,
        getOldEscrow(network),
        buildWithdraw(network, amount),
    );
}

export async function proposeAddOracle(network: string, address: string): Promise<string> {
    return propose(
        network,
        getOracles(network),
        buildAddOracle(address),
    );
}

export async function proposeUpdateWhitelist(network: string, address: string, approved: boolean): Promise<string> {
    return propose(
        network,
        getWhitelistManager(network),
        buildUpdateWhitelist(address, approved),
    );
}

export async function proposeRewardTransfer(network: string, address: string, amount: ethers.BigNumberish): Promise<string> {
    return propose(
        network,
        getRewardETH(network),
        buildRewardEthTransfer(address, amount),
    );
}