import * as React from "react";
import * as ethers from "ethers";

import { ethereum } from "./utils";
import * as safe from "../safe";

export interface MoveProps {
    network: string,
    accounts: string[],
};

function Move(props: MoveProps): React.JSX.Element {
    // State.
    const [initial, setInitial] = React.useState<ethers.BigNumberish>(0);
    const [amount, setAmount] = React.useState<ethers.BigNumberish>(0);
    const [result, setResult] = React.useState<string>("");

    // Update initial balance.
    React.useEffect(() => {
        if (!Boolean(ethereum())) {
            return;
        }
        (async function () {
            if (Boolean(props.network)) {
                const initial = await safe.getBalance(props.network);
                setInitial(initial);
                setAmount(initial);
            }
        }());
    }, [props.network]);

    // Event handler: amount input changed.
    function handleAmount(event: React.ChangeEvent<HTMLInputElement>): void {
        try {
            const wei: ethers.BigNumber = ethers.utils.parseEther(event.target.value);
            if (wei.gt(0)) {
                setAmount(wei);
            }
        } catch (e) {
            console.log("" + e);
            setAmount(0);
        }   
    }

    // Event handler: propose move button clicked.
    function handlePropose(_event: React.MouseEvent): void {
        (async function (): Promise<void> {
            const eth: string = ethers.utils.formatEther(amount);
            console.log("" +
                "[INFO] Proposing move tx: " +
                `network=${props.network} ` +
                `amount=${amount} ` +
                `eth=${eth}`
            );
            const result: string = await safe.proposeMove(props.network, amount);
            setResult(result);
        }());
    }

    // Element.
    let feedback: JSX.Element = (<></>);
    let content: JSX.Element = (<></>);

    if (result.startsWith("http")) {
        // Result was set to a URL, which means a move proposal was
        // successfully submitted.
        feedback = (
            <div className="alert alert-success">
                Transaction proposed, visit your &nbsp;
                <a href={ result } rel="noreferrer" target="_blank">{ "{SafeWallet}" }</a>
            </div>
        );
    } else if (result !== "") {
        // Result was set to a message, which we should now display.
        feedback = (<div className="alert alert-danger"> { result } </div>);
    }

    if (props.accounts != null && props.accounts.length > 0) {
        const eth: string = ethers.utils.formatEther(initial);
        const code: string = `[Old]PoolEscrow.withdraw(${eth}Ξ)`;
        content = (
            <>
                <code>{ code }</code>
                <div className="mb-3 input-group">
                    <span className="input-group-text" id="move-amount-addon">Move amount</span>
                    <input
                        aria-describedby="move-amount-addon"
                        aria-label="Amount"
                        className="form-control"
                        defaultValue={ eth }
                        onChange={ handleAmount }
                        placeholder="Amount"
                        type="text" />
                    <button
                        className="btn btn-outline-light"
                        onClick={ handlePropose }
                        type="button">
                            Propose
                    </button>
                </div>
                { feedback }
            </>
        );
    } else {
        return (<></>);
    }

    return (
        <div className="row">
            <div className="section">
                <h5>Move funds from the old escrow contract to the new one</h5>
                { content }
            </div>
        </div>
    );
}

export default Move;
