import * as ethers from "ethers";
import React from 'react';

import { ethereum } from "./utils";
import UpdateWhitelist from './UpdateWhitelist'; 
import Move from './Move';

import brand from "./brand60.png";
import './App.css';
import MoveRewardETH from "./MoveRewardETH";

function App() {
  const [network, setNetwork] = React.useState<string>("");
  const [accounts, setAccounts] = React.useState<Array<string>>([]);

  // Update network.
  React.useEffect(() => {
    if (!Boolean(ethereum())) {
      return;
    }
    (async function () {
      const chain = await ethereum()?.request({ method: "eth_chainId" });
      const parsed: ethers.BigNumber = ethers.BigNumber.from(chain);
      if (parsed.eq(1)) {
        setNetwork("mainnet");
      } else if (parsed.eq(5)) {
        setNetwork("goerli");
      }
    }());
  }, []);

  // Event handler: connect button clicked.
  function handleConnect(_event: React.MouseEvent<Element>): void {
    (async function (): Promise<void> {
      const accounts: Array<string> = await ethereum()?.request({
        method: "eth_requestAccounts",
      });
      setAccounts(accounts);
    }());
  }

  //   } else if (!Boolean(ethereum())) {
  //   // There's no result and no MetaMask.
  //   return (<p className="move-text">MetaMask not detected</p>);
  // } else if (accounts != null && accounts.length > 0) {
  //   // There's no result, there is Metamask it's now connected.
  //   const eth = ethers.utils.formatEther(initial);
  //   return (
  //     <div className="section move-section">
  //       <p className="move-text">
  //         Wallet: { accounts[0] }

  let wallet: JSX.Element = (<></>);

  if (!Boolean(ethereum())) {
    wallet = (
      <a href="https://metamask.io/" target="_blank" rel="noreferrer">Please install MetaMask first</a>
    );
  } else if (accounts != null && accounts.length > 0) {
    const account: string = accounts[0];
    const prefix: string = (network === "goerli") ? "goerli." : "";
    const etherscan: string = `https://${prefix}etherscan.io/address/${account}`;
    wallet = (
      <>
        Wallet:&nbsp;
        <a href={ etherscan } target="_blank" rel="noreferrer">
          { account }
        </a>
        @{ network }
      </>
    );
  } else {
    wallet = (
      <button className="btn btn-outline-dark my-2 my-sm-0" onClick={ handleConnect }>
        Connect Wallet
      </button>
    );
  }

  let body = <></>;
  if (network !== "") {
    body = (
      <>
        <Move network={network} accounts={accounts} />
        <MoveRewardETH network={network} accounts={accounts} />
        <UpdateWhitelist network={network} accounts={accounts} />
      </>
    );
  }

  return (
    <>
      <nav className="navbar navbar-light app-nav-bg">
        <div className="container">
          <a className="navbar-brand" href="/">
            <img src={ brand } height="30" alt="" />
          </a>
          <div className="form-inline my-2 my-lg-0">
            { wallet }
          </div>
        </div>
      </nav>

      <div className="container">
          {body}
      </div>
    </>
  );
}

export default App;
