import * as React from "react";
import * as ethers from "ethers";

import { ethereum } from "./utils";
import * as safe from "../safe";

export interface MoveRewardETHProps {
    network: string,
    accounts: string[],
};

function MoveRewardETH(props: MoveRewardETHProps): React.JSX.Element {
    // State.
    const [amount, setAmount] = React.useState<ethers.BigNumberish>(0);
    const [address, setAddress] = React.useState<string>(safe.getRewardRecipient(props.network));
    const [result, setResult] = React.useState<string>("");

    // Update initial balance.
    React.useEffect(() => {
        if (!Boolean(ethereum())) {
            return;
        }
        (async function () {
            if (Boolean(props.network)) {
                const initial = await safe.getRewardBalance(props.network, safe.getMultisig(props.network));
                setAmount(initial);
            }
        }());
    }, [props.network]);

    // Event handler: amount input changed.
    function handleAddress(event: React.ChangeEvent<HTMLInputElement>): void {
        try {
            const norm: string = ethers.utils.getAddress(event.target.value);
            setAddress(norm);
        } catch (e) {
            console.log("" + e);
            setAddress("");
        }
    }

    // Event handler: propose move button clicked.
    function handlePropose(_event: React.MouseEvent): void {
        (async function (): Promise<void> {
            console.log("" +
                "[INFO] Proposing RewardETH.transfer tx: " +
                `network=${props.network} ` +
                `address=${address} ` +
                `amount=${amount}`
            );
            const result: string = await safe.proposeRewardTransfer(props.network, address, amount);
            setResult(result);
        }());
    }

    // Element.
    let feedback: JSX.Element = (<></>);
    let content: JSX.Element = (<></>);

    if (result.startsWith("http")) {
        // Result was set to a URL, which means a move proposal was
        // successfully submitted.
        feedback = (
            <div className="alert alert-success">
                Transaction proposed, visit your &nbsp;
                <a href={ result } rel="noreferrer" target="_blank">{ "{SafeWallet}" }</a>
            </div>
        );
    } else if (result) {
        // Result was set to a message, which we should now display.
        feedback = (<div className="alert alert-danger"> { result } </div>);
    }
    // transfer(address recipient, uint256 amount)
    if (props.accounts != null && props.accounts.length > 0) {
        const code: string = `RewardETH.transfer(${address}, ${amount})`;
        content = (
            <>
                <code>{ code }</code>
                <div className="mb-3 input-group">
                    <span className="input-group-text" id="move-amount-addon">Address</span>
                    <input
                        aria-describedby="move-amount-addon"
                        aria-label="Address"
                        className="form-control"
                        onChange={ handleAddress }
                        placeholder="Address"
                        defaultValue={ address }
                        type="text" />
                    <button
                        className="btn btn-outline-light"
                        onClick={ handlePropose }
                        type="button">
                            Propose
                    </button>
                </div>
                { feedback }
            </>
        );
    } else {
        return (<></>);
    }

    return (
        <div className="row">
            <div className="section">
                <h5>Move RewardETH</h5>
                { content }
            </div>
        </div>
    );
}

export default MoveRewardETH;
